<template>

    <div id="body">

      <div id="Alpha" v-show="floatWindows"></div>

      <input id="one" placeholder="请输入内容并回车" v-model="link"  @keydown.enter="useQrcode();floatWindows=true">
      <div id="F-fonts">
        <div id="fonts" onclick="window.open('http://c2595918590.github.io/','_self','');" style="flex-wrap: wrap;">

          <span style="--i:1"></span>
          <span style="--i:2">年</span>
          <span style="--i:3"></span>
          <span style="--i:4">月</span>
          <span style="--i:5"></span>
          <span style="--i:6">日</span>
          <div>
            <span style="--i:7"></span>
            <span style="--i:8">时</span>
            <span style="--i:9"></span>
            <span style="--i:10">分</span>
            <span style="--i:11"></span>
            <span style="--i:12">秒</span>
        </div>



        </div>
      </div>
<div style="display: flex;align-items: center;justify-content: center;height: 300px">
          <div id="recode" v-show="floatWindows" >
             <div><h2 style="border: 1px solid white;">请扫码</h2></div>
              <div><canvas id="canvas"></canvas></div>
              <button id="close" @click="link=null;floatWindows=false">x</button>
          </div>

        </div>

    </div>
</template>

<script >
import QRCode from "_qrcode@1.5.0@qrcode";
export default {
  name: "dome",
  data() {
    return {
      link: null,
      floatWindows: false,
      num:null,
      name:[
        '赵',
        '李',
        '孙',
      ]
    }
  },
  methods: {
    useQrcode() {
      // noinspection JSUnresolvedVariable
      // this.link = document.getElementById('one').value;
      QRCode.toCanvas(document.getElementById("canvas"), this.link , function (error) {
        if (!error) {
          console.log('成功');
          // return  console.log(this.$data.floatWindows);

        } else {
          console.log('生成失败，请重试！！！！');
          // console.log('成功');
        }
      });
      // console.log(this.link);
      // this.link=null;
      console.log(this.$data.floatWindows)

    },


  },
  mounted() {
    let time =new Date()
    let H=document.querySelectorAll("div span")
    H[0].innerText=time.getFullYear();
    H[2].innerText=time.getMonth()+1;
    H[4].innerText=time.getDate();
    H[6].innerText=time.getHours();

    setInterval(
      ()=>{
        let time =new Date()
        H[8].innerText=time.getMinutes();
        H[10].innerText=time.getSeconds();
      },1000)
  }
}

</script>

<style scoped >
  *{
    /*border: 1px solid white;*/
  }

  body,#F-fonts{
    /*width: 100%;*/
    /*height: 100vh;*/
    margin: 0;
  }
  #one{
    float-displace: auto;
    margin: 20px;
  }
  #F-fonts{

    /*background-color: #111;*/
    display: flex;
    justify-content: center;
    /*align-items:center;*/
    min-height: 10vw;
    /*height: 100%;*/
  }
  #fonts{
    font-family: '站酷小薇LOGO体',serif;
    display: flex;
    justify-content: center;
    color: transparent;
    font-size: 10vw;

  }
  #fonts span{
    animation: animate 2.5s linear infinite;
    animation-delay: calc(0.1s*var(--i));
  }
  @keyframes animate {
    0%{
      color:#FFF;
      filter:blur(0px) hue-rotate(0deg);
      text-shadow:
                    /*0 0 10px #00b3ff,*/
                   0 0 20px #00b3ff,
                   0 0 40px #00b3ff,
                   0 0 80px #00b3ff;
                   /*0 0 120px #00b3ff,*/
                   /*0 0 200px #00b3ff,*/
                   /*0 0 300px #00b3ff,*/
                   /*0 0 400px #00b3ff;*/
    }
    30%,70%{
      color:#FFF;
      filter:blur(0px) hue-rotate(360deg);
      text-shadow:
                    0 0 10px #00b3ff,
                    0 0 20px #00b3ff,
                    /*0 0 40px #00b3ff,*/
                    0 0 80px #00b3ff;
                    /*0 0 120px #00b3ff,*/
                    /*0 0 200px #00b3ff,*/
                    /*0 0 300px #00b3ff,*/
                    /*0 0 400px #00b3ff;*/
    }
    100%{
      color: transparent;
      box-shadow: none;
      filter:blur(1px) hue-rotate(0deg);
    }
  }
  #recode{
    position: absolute;
    /*top: 23vh;*/
    /*left: 39vw;*/
    z-index: 3;
    display: grid;
    grid-template: 30% 70%/100%;
    align-items: center;
    justify-content: center;
    width:30vh;
    height:40vh;
    background-color: #fffcdb;
    border-radius: 5%;
  }
  #recode div{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  #close{
    position: absolute;
    top: 10px;
    right: 10px;

    font-size: 25px;
    color: #333;
    z-index: 10;
  }
  #close:hover{
    color:red;
  }



  #Alpha{
    margin: 0;
    position:absolute;
    z-index:1;
    width:100%;
    height:100vh;
    background-color:rgba(26,26,26,0.58);
    filter:Alpha(Opacity=50);
  }





</style>



