<template>
    <div class="stars">
      <div v-for="(item,index) in starsCount" :key="index" class="star" ref="star">
      </div>

    </div>
</template>

<script>


export default {
  data(){
    return{
      starsCount:250,//数量
      distance:75,//间距
    }
  },
methods:{
    change(){
      let _this=this;
      let starArr=_this.$refs.star;
      // console.log(starArr);
      starArr.forEach(item=>{
        let speed = 0.2 + (Math.random());
        let thisDistance = _this.distance + (Math.random() * 500);
        item.style.transformOrigin=`0 0 ${thisDistance}px`;
        item.style.transform=`translate3d(0,0,-${thisDistance}px) rotateY(${(Math.random() * 360)}deg) rotateX(${(Math.random() * -50)}deg) scale(${speed},${speed})`
      })
    }
},
  mounted() {
    this.change()
  },



}
</script>

<style scoped>

@keyframes rotate {
  0%{
    transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);

  }
  100%{
    transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
  }
}
.stars{
  transform: perspective(500px);
  transform-style: preserve-3d;
  position: absolute;
  perspective-origin: 50% 100%;
  left: 50%;
  bottom: 0;
  animation: rotate 60s infinite linear;
}
.star{
  width: 2px; height: 2px;
  background: rgb(252, 234, 41);
  /*box-shadow: 0 0 10px #fffcdb;*/
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;
}
#number{
  margin: 20px;
  float: right;
  /*top: 20px;*/
  /*right: 20px;*/
}
</style>



