<template>
  <div class="container">
    <div class="ring"></div>
    <div class="ring"></div>
    <div class="ring"></div>
    <p> 正在加载中.....</p>
  </div>
</template>

<script>
export default {
  name: "Loading"
}
function f(...name) {
  if(name.length===1){
    console.log(name[0])
  }else if(name.length===2){
    console.log(name[0],name[1])
  }else if(name.length===3){
    console.log(name[0],name[1],name[2])
  }else {
    console.log('错误')
  }

}

f('史伟龙','程宇航','赵文婷')
</script>

<style scoped>

  *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /*border: 1px solid aqua;*/
  }
  body{

    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: black;
  }
  .container{
    margin-top: 150px;
    margin-left: 150px;
    position: relative;
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container .ring{
    position: relative;
    width: 100%;
    height: 100%;
    margin: -30px;
    border-radius: 50%;
    border: 4px solid transparent;
    border-top-color:  #24ecff;
    animation: animate 4s linear infinite;
  }
  @keyframes animate {
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
  .container .ring::before{
    content: '史';
    font-size: 5px;
    position: absolute;
    top: 9.6%;
    right: 9.6%;
    border-radius: 50%;
    width: 10%;
    height: 10px;
    background-color: #24ecff;
    box-shadow: 0 0 0 5px #24ecff88,
    0 0 0 12px #24ecff66,
    0 0 0 20px #24ecff44;
  }


  .container .ring:nth-child(2){
    animation:animate2 4s linear infinite;
    animation-delay: -2s;
    border-top-color: transparent;
    border-right-color: #93ff2d;
  }
  @keyframes animate2 {
    0%{
      transform: rotate(360deg);
    }
    100%{
      transform: rotate(0deg);
    }
   }
  .container .ring:nth-child(2):before{
    content: '伟';
    font-size: 5px;
    position: absolute;
    top: 9.6%;
    bottom: 9.6%;
    border-radius: 50%;
    width: 10%;
    height: 10%;
    background-color: #93ff2d;
    box-shadow: 0 0 0 5px #93ff2d88,
    0 0 0 12px #93ff2d66,
    0 0 0 20px #93ff2d44;
  }


  .container .ring:nth-child(3){
    position: absolute;
    top: -44.4%;
    animation:animate2 4s linear infinite;
    animation-delay:-3s;
    border-top-color: transparent;
    border-right-color: #fc00a5;

  }
  .container .ring:nth-child(3):before{
    content: '龙';
    font-size: 5px;
    position: absolute;
    top: 9.6%;
    bottom: 9.6%;
    border-radius: 50%;
    width: 10%;
    height: 10%;
    background-color: #fc00a5;
    box-shadow: 0 0 0 5px #fc00a588,
    0 0 0 12px #fc00a566,
    0 0 0 20px #fc00a544;
  }



  .container p{
    position: absolute;
    top: 200px;
    font-size: 2em;
    color: white;
    letter-spacing: 0.15em;
  }
</style>



